export const links = [{ rel: "stylesheet", href: s }];
export const Label = ({title,color})=>{
return (
  <Box
    className={"typetask"}
    
    justifyContent="center"    
    flexDirection="row"
    style={{
      backgroundColor: color,
    }}
  >
    <BaseText
      className={"typetask_inovao"}
      type="span"
      title={title}
    ></BaseText>
  </Box>
);
            
}
export const Card = ({data})=>{

  return (
    <Box className={"card"} flexDirection="row">
      <Box
        className={"card_infocard"}
        alignItems="center"
        flexDirection="column"
      >
        <Box
          className={"card_header"}
          flexDirection="column"
        >
          <Box className={"card_tags"}  flexDirection="row" alignItems="center" justifyContent="center">
          <Box   flexDirection="row" flex={1}  alignItems="center">
              {data.labels.map((e,k)=>{
                return <Label title={e.name} color={e.color} key={k}/>
              })}
          </Box>
          <Box   flexDirection="row">
              {data.members.map((e,k)=>{
                return <span style={{
                  width: "18px",
                  height: "18px",
                  borderRadius:"100px",marginRight:"5px",
                  overflow:"hidden"}}
                  data-tip={e.name}
                ><BaseImage src={e.avatar} key={k} width="18px" height="18px"/></span>
              })}
          </Box>
          </Box>

          <BaseText
            className={"card_reconstruoregistrocorreodeponto"}
            type="span"
            title={e.name}
          ></BaseText>
        </Box>

        <Box className={"card_info"} alignItems="center" flexDirection="row">
          <Box
            className={"card_deadline"}
            alignItems="center"
            flexDirection="row"
          >
            <Icon name="icon-icon_calendar1" />

            <BaseText
              className={"card_"}
              type="span"
              title={data.dateDeadLine}
            ></BaseText>
          </Box>

          <Box
            className={"card_priority"}
            alignItems="center"
            flexDirection="row"
          >
            <Icon name="icon-icon_alert" />

            <BaseText
              className={"card_baixa"}
              type="span"
              title={"Baixa"}
            ></BaseText>
          </Box>

          <a
            href={data.url}
            className={"card_trello"}
            alignItems="center"
            flexDirection="row"
            target="_blank"
          >
            <Icon name="icon-icon_trello" />

            <BaseText
              className={"card_cardtrello"}
              type="span"
              title={"Card Trello"}
            ></BaseText>
          </a>
        </Box>
      </Box>
    </Box>
  );               
}

export const ColumnCards=({data,style})=>{

    const dataFiltered = useMemo(() => {

        let finish = [];
        let proccess = [];

        data?.data?.forEach(e=>{
            if(e.dueComplete){
                finish.push(e)
            }else if(e.start){
                proccess.push(e)
            }
        })

        return {finish,proccess}

    }, [])

    return (
      <Box className={"inovation"} style={style} flexDirection="column">
        <Box className={"inovation_headerlist"} flexDirection="row">
          <Box
            className={"inovation_title"}
            style={{
              width: "100%",
            }}
            flexDirection="row"
          >
            <BaseText
              className={"inovation_inovao"}
              type="span"
              title={data.emoji}
            ></BaseText>

            <BaseText
              className={"inovation_inovao"}
              type="span"
              title={data.title}
            ></BaseText>
          </Box>

          <Box className={"inovation_number"} flexDirection="row">
            <BaseText
              className={"inovation_total"}
              type="span"
              title={dataFiltered.finish.length + dataFiltered.proccess.length}
            ></BaseText>
          </Box>
        </Box>

        <Divider
          type="h"
          spaccing="0px"
          style={{ backgroundColor: "#EBEBED" }}
        ></Divider>

        <Box className={"inovation_inprocess"} flexDirection="column">
          <BaseText
            className={"inovation_emprocesso"}
            type="span"
            title={"EM PROCESSO"}
          ></BaseText>
          <div>
            {dataFiltered?.proccess?.map((e) => {
              return <Card data={e} key={e.id}></Card>;
            })}
          </div>
        </Box>

        <Divider
          type="h"
          spaccing="0px"
          style={{ backgroundColor: "#EBEBED" }}
        ></Divider>

        <Box className={"inovation_completed"} flexDirection="column">
          <BaseText
            className={"inovation_concluidos"}
            type="span"
            title={"CONCLUIDOS"}
          ></BaseText>
          <div>
            {dataFiltered?.finish?.map((e) => {
              return <Card data={e} key={e.id}></Card>;
            })}
          </div>
        </Box>
      </Box>
    );    
}
